$bgColor1: hsl(156, 64%, 91%);
$bgColor2: hsl(156, 64%, 50%);

$textColor1: hsl(0, 10%, 0%);
$textColor2: hsl(0, 10%, 40%);

$fs-800: clamp(25px, 2.6vw, 50px);
$fs-600: clamp(20px, 2.1vw, 40px);
$fs-400: clamp(10px, 1.1vw, 20px);
$fs-200: clamp(8px, 0.8vw, 16px);

$padding-800: $fs-800;
$padding-600: $fs-600;
$padding-400: $fs-400;
$padding-200: $fs-200;

$line-height-800: 2em;
$line-height-600: 1.5em;

$letter-spacing-800: 0.4em;
$letter-spacing-600: 0.2em;
@import 'reset';

@mixin link($fSize, $color, $fWeight) {
  text-transform: uppercase;
  font-size: $fSize;
  color: $color;
  transform: scale(1);
  font-weight: $fWeight;
  transition: transform 1000ms;
  &:hover,
  &:focus-visible {
    transform: scale(1.5);
    outline: transparent;
    transition: transform 1000ms;
  }
}

@mixin text($fSize, $color, $fWeight, $lHeight, $lSpacing) {
  font-size: $fSize;
  color: $color;
  font-weight: $fWeight;
  line-height: $lHeight;
  letter-spacing: $lSpacing;
  text-align: center;
}
/*  hader and footer */
header {
  padding: $padding-800;
  background-color: $bgColor1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  a {
    text-align: center;
    @include link($fs-600, $textColor1, 800);
    margin-bottom: 1vw;
  }
  nav {
    display: flex;
    justify-content: space-between;
    gap: clamp(8px, 1vw, 20px);
    a {
      @include link($fs-400, $textColor1, 500);
      margin: 0 $padding-200;
    }
  }
}

@media screen and (min-width: 800px) {
  header {
    flex-direction: row;

    a {
      text-align: end;
      margin-bottom: 0;
    }
    nav {
      a {
        display: block;
      }
    }
  }
}

footer {
  padding: $padding-800;
  background-color: $bgColor1;
  small {
    @include text(
      $fs-400,
      $textColor1,
      500,
      $line-height-600,
      $letter-spacing-600
    );
  }
}
