.button {
  cursor: pointer;
  text-align: center;
  border: 5px solid $bgColor1;
  background-color: transparent;
  border-radius: 20px;
  color: $textColor1;
  position: relative;
  isolation: isolate;
  overflow: hidden;
  transition: color 1000ms, border-color 1000ms;
  &:focus-visible,
  &:hover {
    border-color: $bgColor1;
    outline: transparent;
    transition: color 1000ms, border-color 1000ms;
  }
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    background: $bgColor1;
    width: 10%;
    aspect-ratio: 1;
    border-radius: 50%;
    inset: 0;
    margin: auto;
    opacity: 0;
    transform: scale(1);
    transition: transform 1000ms, opacity 1000ms;
  }
  &:focus-visible::before,
  &:hover::before {
    transform: scale(20);
    opacity: 1;
    transition: transform 1000ms, opacity 500ms;
  }
}

.button1 {
  flex-basis: 100% !important;
  &::before {
    content: '';
    transition: transform 1500ms, opacity 2000ms;
  }
  &:focus-visible::before,
  &:hover::before {
    transition: transform 2000ms, opacity 1000ms;
  }
}

